.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

footer {
  padding: 50px 200px;
  margin-top: 5rem;
  color: #09d3ac;
  font-size: 12px;
  letter-spacing: 0;
}
footer p {
  margin: 0;
}
